<script>
import TopBlock from '@/components/TopBlock.vue'
import Map from '@/components/Map.vue'
import Contact from '@/components/Contact.vue'
import MidPlain from '@/components/MidPlain.vue'
import BrandTile from '@/components/BrandTile.vue'
import LensTile from '@/components/LensTile.vue'

export default {
  name: 'Oferta',
  components: {
    TopBlock,
    Map,
    Contact,
    MidPlain,
    BrandTile,
    LensTile
  },
  methods: {
    orderBrandTiles() {
      if (window.innerWidth < 768) {
        return
      }

      const rodenstock = document.querySelector('#rodenstock')
      const tavat = document.querySelector('#tavat')
      const kuboraum = document.querySelector('#kuboraum')

      const lensL = document.querySelector('#lens-l')
      const lensM = document.querySelector('#lens-m')
      const lensR = document.querySelector('#lens-r')

      lensL.style.marginTop = `${lensM.scrollHeight / 4}px`
      lensR.style.marginTop = `${lensM.scrollHeight / 4}px`

      rodenstock.style.marginTop = `${tavat.scrollHeight / 2}px`
      kuboraum.style.marginTop = `${tavat.scrollHeight / 2}px`

      if (kuboraum.scrollHeight > rodenstock.scrollHeight) {
        const diff = kuboraum.scrollHeight - rodenstock.scrollHeight
        rodenstock.style.paddingBottom = `${diff}px`
      } else {
        const diff = rodenstock.scrollHeight - kuboraum.scrollHeight
        kuboraum.style.paddingBottom = `${diff}px`
      }
    }
  },
  mounted() {
    this.orderBrandTiles()
  }
}
</script>

